import { useEffect, useState } from "react";
import { supabase } from "../../database/supabaseClient";
import React from "react";

const AuthStatusPage = () => {
  const [session, setSession] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const getSession = async () => {
      const { data, error } = await supabase.auth.getSession();
      if (!error) setSession(data.session);
      setLoading(false);
    };

    getSession();

    // Listen for login/logout/refresh
    const { data: listener } = supabase.auth.onAuthStateChange(
      (_event, newSession) => {
        setSession(newSession);
      }
    );

    return () => listener.subscription.unsubscribe();
  }, []);

  if (loading) return <div>Loading auth statusâ€¦</div>;

  return (
    <div style={{ padding: "30px", fontFamily: "monospace" }}>
      <h1>ğŸ” Auth Status</h1>

      {!session ? (
        <div style={{ color: "red" }}>âŒ No user logged in</div>
      ) : (
        <div>
          <h2>ğŸ‘¤ User</h2>
          <pre style={{ background: "#111", color: "#0f0", padding: "20px" }}>
{JSON.stringify(session.user, null, 2)}
          </pre>

          <h2>ğŸ”‘ Access Token</h2>
          <pre style={{ background: "#111", color: "#0ff", padding: "20px" }}>
{session.access_token}
          </pre>

          <h2>ğŸ”„ Refresh Token</h2>
          <pre style={{ background: "#111", color: "#ff0", padding: "20px" }}>
{session.refresh_token}
          </pre>

          <h2>ğŸ“¦ Full Session Payload</h2>
          <pre style={{ background: "#222", color: "#fff", padding: "20px" }}>
{JSON.stringify(session, null, 2)}
          </pre>
        </div>
      )}
    </div>
  );
};

export default AuthStatusPage;
